import { useState } from 'react';
import { Form, Input, Button, Checkbox, Flex, notification } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

// import './entry-page.css'

const EntryPage = () => {
  const nav = useNavigate();
  const [api, contextHolder] = notification.useNotification();
  const [loginForm] = Form.useForm()
  const [singUpForm] = Form.useForm()
  const [PWResetForm] = Form.useForm()


  const [view, setView] = useState('login')
  const changeView = (view) => {
    loginForm && loginForm?.resetFields()
    singUpForm && singUpForm?.resetFields()
    PWResetForm && PWResetForm?.resetFields()
    setView(view)
  }

  const onFinishLogin = (values) => {
    console.log(':', values);
    loginForm.resetFields()
    nav('/home')
  };
  const onFinishRegister = (values) => {
    console.log(':', values);
    api.success({
      message: '',
      duration: 1.5
    })
    changeView('login')
  };
  const onFinishResetPW = (values) => {
    console.log('', values)
    api.success({
      message: '',
      duration: 1.5
    })
    changeView('login')
  }

  const currentView = () => {
    switch (view) {
      case "signUp":
        return (
          <Form
            name="signUp"
            form={singUpForm}
            onFinish={onFinishRegister}
            variant='borderless'
          >
            <div className="login-title"></div>
            <Form.Item
              name="username"
              className='register-input'
              rules={[{ required: true, message: '', whitespace: true }]}
            >
              <Input placeholder='' />
            </Form.Item>

            <Form.Item
              name="password"
              className='register-input'
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
              hasFeedback
            >
              <Input.Password placeholder='' />
            </Form.Item>

            <Form.Item
              name="confirm"
              className='register-input'
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: '',
                },
                ({ getFieldValue }) => ({
                  validator (_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error(''));
                  },
                }),
              ]}
            >
              <Input.Password placeholder='' />
            </Form.Item>

            <Form.Item
              name="question"
              className='register-input'
              rules={[{ required: true, message: '' }]}
            >
              <Input.TextArea style={{ height: 100 }} placeholder="" showCount maxLength={100} />
            </Form.Item>

            <Form.Item
              name="agreement"
              valuePropName="checked"
              rules={[
                {
                  validator: (_, value) =>
                    value ? Promise.resolve() : Promise.reject(new Error('')),
                },
              ]}
            >
              <Checkbox>
                 
              </Checkbox>
            </Form.Item>
            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' type="primary" htmlType="submit">
                
              </Button>
              <span className='register' onClick={() => changeView('login')}></span>
            </Form.Item>
          </Form>
        )
        break
      case "login":
        return (
          <Form
            form={loginForm}
            name="login"
            initialValues={{ remember: true }}
            onFinish={onFinishLogin}
            variant='boderless'
          >
            <div className="login-title"></div>
            <Form.Item
              name="username"
              className='username'
              rules={[{ required: true, message: '' }]}
            >
              <Input prefix={<UserOutlined />} placeholder="" />
            </Form.Item>
            <Form.Item
              name="password"
              className='password'
              rules={[{ required: true, message: '' }]}
            >
              <Input prefix={<LockOutlined />} type="password" placeholder="" />
            </Form.Item>
            <Form.Item>
              <Flex justify="space-between" align="center">
                <Form.Item className='remember' name="remember" valuePropName="checked" noStyle>
                  <Checkbox></Checkbox>
                </Form.Item>
                <span className='reset-password' onClick={() => changeView('PWReset')}></span>
              </Flex>
            </Form.Item>

            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' block type="primary" htmlType="submit">
                 
              </Button>
              <span className='register' onClick={() => changeView('signUp')}>!</span>
            </Form.Item>
          </Form>
        )
        break
      case "PWReset":
        return (
          <Form
            name="PWReset"
            form={PWResetForm}
            onFinish={onFinishResetPW}
            variant='borderless'
          >
            <div className="login-title"></div>
            <Form.Item
              name="username"
              className='register-input'
              rules={[{ required: true, message: '', whitespace: true }]}
            >
              <Input placeholder='' />
            </Form.Item>

            <Form.Item
              name="password"
              className='register-input'
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
              hasFeedback
            >
              <Input.Password placeholder='' />
            </Form.Item>

            <Form.Item
              name="confirm"
              className='register-input'
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: '',
                },
                ({ getFieldValue }) => ({
                  validator (_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error(''));
                  },
                }),
              ]}
            >
              <Input.Password placeholder='' />
            </Form.Item>

            <Form.Item
              name="question"
              className='register-input'
              rules={[{ required: true, message: '' }]}
            >
              <Input.TextArea style={{ height: 100 }} placeholder="" showCount maxLength={100} />
            </Form.Item>

            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' type="primary" htmlType="submit">
                
              </Button>
              <span className='register' onClick={() => changeView('login')}></span>
            </Form.Item>
          </Form>
        )
      default:
        break
    }
  }

  return <section className='entry-page'>
    <div className='login-wrapper'>
      <div className="back-img"></div>
      {currentView()}
    </div>
    {contextHolder}
  </section>
}

export default EntryPage
